<template>
    <div class="shoppingcar">
        <div class="bg">
            <p>我的购物车</p>
        </div>
        <p class="pp"><span>产品</span><span>价格</span><span>数量</span><span>总价</span></p>
        <div v-for="(item,index) in shoplist" :key="index">
            <div class="div1"><i class="el-icon-close"  @click="close(index)"></i><img :src="item.img " alt=""></div>
            <p>{{item.nickname}}</p>
            <p>{{item.money}}元</p>
             <p><button @click="jian(item)">-</button><span>{{item.count}}</span><button @click="jia(item)">+</button></p>
            <p>{{item.money*item.count}}</p>
        </div>
        <p class="ppp">购物车总数</p>
        <p class="pppp"><span>小计</span><span>{{totalPrice}}元</span></p>
        <p class="pppp"><span>全部</span><span>{{totalPrice}}元</span></p>
        <p class="ppppp"><button @click="tijiao" class="btn">提交订单</button></p>
        <div class="foot" v-show="isshow">
            <div>
                <div>
                     <span><span>产品价格</span><span>￥{{totalPrice}}</span></span>
                    <span><span>优惠金额</span><span>￥0</span></span>
                    <span><span>运费</span><span>￥0</span></span>
                </div>
                <span class="sp"><span>总价</span><span>￥{{totalPrice}}</span></span>
                <span class="ff"><button @click="isshow=false">取消</button><button @click="dell(shoplist)">确定</button></span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:['shoplist'],
    data(){
        return{
              num:1,
              isshow:false
        }
    },
      computed:{
        totalPrice:function(){
            var totalPrice=0;
            var len=this.shoplist.length;
            for(var i=0;i<len;i++){
                totalPrice+=this.shoplist[i].money*this.shoplist[i].count;
            }
            return totalPrice;
        },
       
    },
    created(){
         console.log(this.shoplist);
    },
    methods:{
        jian(item){
            item.count--
            if(item.count<=0){
                item.count=1
            }
        },
        jia(item){
            item.count++
        },
        del(item){
            item==null
        },
          close(index){
              this.shoplist.splice(index,1)
        },
        tijiao(){
            this.isshow=true
        },
        dell(e){
            e.splice(0);
            this.isshow=false
        }
    }
}
</script>
